import React from 'react';
import Layout from '../../components/Layout';
import Me from './Me';

export default {

  path: '*',

  async action(req) {
    let user = req.user;
    return {
      title: 'I',
      component: <Layout><Me me={user}/></Layout>,
    };
  },

};



